<template>
    <el-container>
        <el-header>
            <container-top></container-top>
        </el-header>
        <el-container>
            <nav-header></nav-header>
            <el-main>
                <el-container class="container-main-div">
                    <div class="container-div">
                        <div class="container-main">
                            <div class="top-main">
                                <div class="page-top-line">
                                    <div class="page-title">节点管理</div>
                                </div>
                                <div class="screen-block">
                                    <el-button class="create-node" @click="handleAdd" icon="el-icon-plus">创建节点</el-button>
                                </div>
                            </div>
                            <div class="out-div">
                                <div class="node-list">
                                    <template>
                                        <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%">
                                            <el-table-column prop="id" label="序号" width="80">
                                                <template slot-scope="scope">
                                                    <span>{{(currentPage - 1) * pagesize + scope.$index + 1}}</span>
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="nodeName" label="节点名称"></el-table-column>
                                            <el-table-column prop="logo" label="logo">
                                                <template slot-scope="scope">
                                                    <img :src="scope.row.logo" alt="" height="30px">
                                                </template>
                                            </el-table-column>
                                            <el-table-column prop="ipAddress" label="IP地址"></el-table-column>
                                            <el-table-column prop="port" label="端口号"></el-table-column>
                                            <el-table-column prop="status" label="状态">
                                                <template slot-scope="scope">
                                                    <p v-if="scope.row.status">接通</p>
                                                    <p v-else>失败</p>
                                                </template>
                                            </el-table-column>
                                            <el-table-column label="操作" width="200px">
                                                <template slot-scope="scope">
                                                    <div class="cell">
                                                        <div @click="handleEdit(scope.$index, scope.row)" class="btns edit-btn">
                                                            <img src="static/images/edit.png" alt="">
                                                            <p>修改</p>
                                                        </div>
                                                        <div @click="handleDelete(scope.$index, scope.row)" class="btns del-btn">
                                                            <img src="static/images/del.png" alt="">
                                                            <p>删除</p>
                                                        </div>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                        <el-pagination
                                            @size-change="handleSizeChange"
                                            @current-change="handleCurrentChange"
                                            :current-page="currentPage"
                                            :page-sizes="[5,10,20,50]"
                                            :page-size="pagesize"
                                            layout="total, sizes, prev, pager, next, jumper"
                                            :total="tableData.length"
                                            style="margin-top:15px">
                                        </el-pagination>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                </el-container>
                <el-dialog title="创建节点" :visible.sync="dialogVisibleAdd" width="500px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <el-form ref="formAdd" :rules="rulesAdd" :model="formAdd" label-width="120px">
                        <el-form-item label="节点名称" prop="nodeName">
                            <el-input v-model="formAdd.nodeName"></el-input>
                        </el-form-item>
                        <el-form-item label="机构logo" style="width: 400px;">
                            <img id="base64Img" :src="headImg"/>
                            <div class="img-div">
                                <p>选择图片</p>
                                <el-input v-model="formAdd.logo" class="add-uploadimg" type="file" @change="imgChange" style="opacity: 0;"></el-input>
                            </div>
                        </el-form-item>
                        <el-form-item label="IP地址" prop="ipAddress">
                            <el-input v-model="formAdd.ipAddress"></el-input>
                        </el-form-item>
                        <el-form-item label="端口号" prop="port">
                            <el-input v-model="formAdd.port"></el-input>
                        </el-form-item>
                        <el-form-item label="CSV文件路径" prop="CSV_path">
                            <el-input v-model="formAdd.CSV_path"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="addAccount('formAdd')">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleAdd = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="修改节点" :visible.sync="dialogVisibleEdit" width="500px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <el-form ref="form" :rules="rulesEdit" :model="form" label-width="120px">
                        <el-form-item label="节点名称" prop="oldName" style="display: none;">
                            <el-input v-model="form.oldName"></el-input>
                        </el-form-item>
                        <el-form-item label="节点名称" prop="nodeName">
                            <el-input v-model="form.nodeName"></el-input>
                        </el-form-item>
                        <el-form-item label="机构logo" style="width: 400px;">
                            <img id="base64Img2" :src="headImg2"/>
                            <div class="img-div">
                                <p>选择图片</p>
                                <el-input v-model="form.logo" class="edit-uploadimg" type="file" @change="imgChange2" style="opacity: 0;"></el-input>
                            </div>
                        </el-form-item>
                        <el-form-item label="IP地址" prop="ipAddress">
                            <el-input v-model="form.ipAddress"></el-input>
                        </el-form-item>
                        <el-form-item label="端口号" prop="port">
                            <el-input v-model="form.port"></el-input>
                        </el-form-item>
                        <el-form-item label="CSV文件路径" prop="CSV_path">
                            <el-input v-model="form.CSV_path"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="editAccount('form')">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleEdit = false">取 消</el-button>
                    </span>
                </el-dialog>
                <el-dialog title="" :visible.sync="dialogVisibleDel" width="400px" :lock-scroll="false" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
                    <img src="static/images/del_tip.png" class="del-tip-style" alt="">
                    <p class="del-tip-p">确定删除？</p>
                    <el-form ref="formDel" :model="formDel" label-width="80px">
                        <el-form-item label="ID" style="display:none">
                            <el-input v-model="formDel.nodeName"></el-input>
                        </el-form-item>
                    </el-form>
                    <span slot="footer" class="dialog-footer">
                        <el-button class="submit-btn-style" type="primary" @click="deleteThis">确 定</el-button>
                        <el-button class="cancle-btn-style" @click="dialogVisibleDel = false">取 消</el-button>
                    </span>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/nodeManage.css";
</style>
<script>
import $ from "jquery";
import Header from "../sub/aside";
import containerTop from "../sub/container_top";
export default {
    name: "nodeManage",
    components: {
        "nav-header": Header,
        "container-top": containerTop,
    },
    data: function() {
        return {
            username:Cookies.get("fedname"),
            token:Cookies.get("fedtoken"),
            tableData: [],
            pagesize:10,
            currentPage:1,
            // 创建账号弹窗
            headImg:'',
            dialogVisibleAdd:false,
            formAdd:{},
            rulesAdd:{
                nodeName:[{ required: true, message: '请填写节点名称', trigger: 'blur' }],
            },
            // 修改弹窗
            headImg2:'',
            dialogVisibleEdit:false,
            form:{},
            rulesEdit:{
                nodeName:[{ required: true, message: '请填写节点名称', trigger: 'blur' }],
            },
            // 删除弹窗
            dialogVisibleDel:false,
            formDel:{},
        }
    },
    mounted(){
        this.findDimensions()
        this.getNodeList()
    },
    methods: {
        // 节点列表
        getNodeList:function(){
            var that = this;
            that.axios.post('/nodeReq',that.qs.stringify({
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.tableData=response.data.nodes
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },
        // 创建节点
        handleAdd:function(){
            var that=this;
            that.dialogVisibleAdd=true;
        },
        imgChange:function(){
            var that=this;
            var file =  document.querySelector('.add-uploadimg input[type=file]').files[0];
            if (file.size >= 1024) {
                this.$message({
                    message: '文件大小超出1K上限',
                    type: 'error'
                });
            }
            else{
                var reader = new FileReader();
                reader.onloadend = function () {
                    $("#base64Img").show();
                    that.headImg=reader.result;
                }
                if (file) {
                    reader.readAsDataURL(file);
                }
            }
        },
        addAccount:function(form){
            var that=this;
            that.$refs[form].validate((valid) => {
                if (valid) {
                    that.dialogVisibleAdd=false;
                    that.axios.post('/nodeCreate',that.qs.stringify({
                        'nodeName':that.formAdd.nodeName,
                        'ipAddress':that.formAdd.ipAddress||'',
                        'port':that.formAdd.port||'',
                        'CSV_path':that.formAdd.CSV_path||'',
                        'logo':that.headImg||'',
                        "operator":that.username,
                        "token":that.token
                    }),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded',
                        }
                    },
                    {
                        emulateJSON:true
                    })
                    .then(function(response){
                        if(response.data.message.state){
                            that.$message({
                                message: '创建节点成功',
                                type: 'success'
                            });
                            that.currentPage=1;
                            that.getNodeList();
                        }
                        else {
                            that.$message.error(response.data.message);
                        }
                    },function(error){
                        that.$message.error(error.response.data.message);
                        if(error.response.status==401){
                            that.$router.push('/');
                            Cookies.set("fedname",1,{expires:-1});
                            Cookies.set("fedrole",1,{expires:-1});
                        }
                    })
                }
                else {
                    return false;
                }
            });
        },
        // 修改
        handleEdit:function(index, rowData){
            var that=this;
            that.dialogVisibleEdit = true;
            that.form = Object.assign({}, rowData);
            that.form.oldName=rowData.nodeName
            that.headImg2=rowData.logo
        },
        imgChange2:function(){
            var that=this;
            var file = document.querySelector('.edit-uploadimg input[type=file]').files[0];
            if (file.size >= 1024) {
                this.$message({
                    message: '文件大小超出1K上限',
                    type: 'error'
                });
            }
            else{
                var reader = new FileReader();
                reader.onloadend = function () {
                    $("#base64Img2").show();
                    that.headImg2=reader.result;
                }
                if (file) {
                    reader.readAsDataURL(file);
                }
            }
        },
        editAccount:function(form){
            var that=this;
            that.$refs[form].validate((valid) => {
                if (valid) {
                    that.dialogVisibleEdit=false;
                    that.axios.post('/nodeModify',that.qs.stringify({
                        'old_nodeName':that.form.oldName,
                        'nodeName':that.form.nodeName,
                        'ipAddress':that.form.ipAddress||'',
                        'port':that.form.port||'',
                        'CSV_path':that.form.CSV_path||'',
                        'logo':that.headImg2||'',
                        "operator":that.username,
                        "token":that.token
                    }),
                    {
                        headers:{
                            'Content-Type':'application/x-www-form-urlencoded',
                        }
                    },
                    {
                        emulateJSON:true
                    })
                    .then(function(response){
                        if(response.data.message.state){
                            that.$message({
                                message: '修改成功',
                                type: 'success'
                            });
                            that.currentPage=1;
                            that.getNodeList()
                        }
                        else {
                            that.$message.error(response.data.message);
                        }
                    },function(error){
                        that.$message.error(error.response.data.message);
                        if(error.response.status==401){
                            that.$router.push('/');
                            Cookies.set("fedname",1,{expires:-1});
                            Cookies.set("fedrole",1,{expires:-1});
                        }
                    })
                }
                else {
                    return false;
                }
            });
        },
        // 删除
        handleDelete(index, rowData) {
            this.dialogVisibleDel = true;
            this.formDel= Object.assign({}, rowData);
        },
        deleteThis:function(){
            var that=this;
            that.dialogVisibleDel=false;
            that.axios.post('/nodeDelete',that.qs.stringify({
                'nodeName':that.formDel.nodeName,
                "operator":that.username,
                "token":that.token
            }),
            {
                headers:{
                    'Content-Type':'application/x-www-form-urlencoded',
                }
            },
            {
                emulateJSON:true
            })
            .then(function(response){
                if(response.data.message.state){
                    that.$message({
                        message: '删除节点成功',
                        type: 'success'
                    });
                    that.currentPage=1;
                    that.getNodeList()
                }
                else {
                    that.$message.error(response.data.message);
                }
            },function(error){
                that.$message.error(error.response.data.message);
                if(error.response.status==401){
                    that.$router.push('/');
                    Cookies.set("fedname",1,{expires:-1});
                    Cookies.set("fedrole",1,{expires:-1});
                }
            })
        },

        handleSizeChange: function (size) {
            this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
        },
        findDimensions:function(){
            if(window.innerWidth)
            this.winWidth = window.innerWidth;
            else if ((document.body) && (document.body.clientWidth))
            this.winWidth = document.body.clientWidth;
            //获取窗口高度
            if(window.innerHeight)
            this.winHeight = window.innerHeight;
            else if((document.body) && (document.body.clientHeight))
            this.winHeight = document.body.clientHeight;
            //通过深入Document内部对body进行检测，获取窗口大小
            if(document.documentElement && document.documentElement.clientHeight && document.documentElement.clientWidth){
                this.winHeight = document.documentElement.clientHeight;
                this.winWidth = document.documentElement.clientWidth;
            }
            $(".node-list").attr("style","min-height:"+(this.winHeight-360)+"px;")
        },
    }
};
</script>



